/**
 * store for page relative to pageManage.
 * page
 */
const mutations = {
	/**
     *  
     */
    init_page(state: Screen.state) {
        state.pageManage.initPage();
    },

    /**
     *  
     */
    add_Pages(state: Screen.state, parentId: string) {
        const pageManage = state.pageManage.addPage();
        pageManage.setParentId(parentId);
    },

	/**
     *  
     */
    remove_page(state: Screen.state, pagesId: string[]): void {
        state.pageManage.removePage(pagesId);
    },

    /**
     *  
     */
    update_current_page(state: Screen.state, pageModel: any): void {
        state.pageManage.updateCurrentPage(pageModel);
    },

	/**
     * 
     */
    move_page(state: Screen.state) {
        state.pageManage.movePage();
    },

    /**
     * 
     */
    copy_page(state: Screen.state, page: Model.Page) {
        state.pageManage.copyPage(page);
    },

    /**
     * ()
     */
    edit_page(state: Screen.state, pageOpt: { page: any, pageName: string, backgroudColor: string }) {
        const { page } = pageOpt;
        state.pageManage.editPage(page, pageOpt);
    }
};
const getter = {
    currentPage: (state: Screen.state) => state.pageManage && state.pageManage.currentPage,
    pagesContainer: (state: Screen.state) => state.pageManage && state.pageManage.pagesContainer,
};

export default {
    mutations,
    getter,
};
