type ElementTag = 'baseLine' | 'dblLine' | 'basePie'
  | 'comparePie' | 'ratioPie' | 'baseBar' | 'baseLineBar'
  | 'verticalGroupBar' | 'horizontalGroupBar' | 'verticalStackBar'
  | 'horizontalStackBar' | 'noneAxisBar' | 'baseText'
  | 'baseRect' | 'baseCircle' | 'baseImg'
  | 'baseTable';

/**
 * element
 * 
 */
export default abstract class Adapter {

  /**
   * element
   */
  public instance: any;

  /**
   * element
   * @param initOption element
   * @param type element
   * @return 
   */
  public init(initOption: any, type?: ElementTag) {
    const defaultOption = this.getDefaultOption(type);
    return this.createOptionFactory(defaultOption, initOption);
  }

  /**
   * element
   * @param defaultOption element,
   *  {
   *    legend: function Legend(opts) {...}
   *    title: function Title(opts) {...}
   *    xAxis: function XAxis(opts) {...}
   *    ...
   *  }
   * @param initOption 
   *  {
   *    legend: {
   *      $position: "top right"
   *      ...
   *    }
   *    title: {
   *      text: ""
   *      ...
   *    }
   *    xAxis: {
   *      $rotate: 360
   *      $textSize: 16
   *      boundaryGap: false
   *      ...
   *    }
   *    ...
   *  }
   * @returns opts 
   */
  public createOptionFactory(defaultOption: any, initOption: any): object {
    if (!_.isPlainObject(defaultOption) || !_.isPlainObject(initOption)) {
      return {}
    }

    const opts: any = {}
    this.instance = {}
    for (let [optionKey, option] of Object.entries(defaultOption)) {
      let initObj: any = {}
      let instance: any = {}
      let Constructor: any = option

      for (let [initKey, value] of Object.entries(initOption)) {
        if (initKey === optionKey) {
          initObj = value
        }
      }

      instance = new Constructor(initObj)
      this.instance[optionKey] = instance
      opts[optionKey] = instance.config
    }
    return opts
  }

  /**
   * element
   */
  public getInstance(): any {
    return this.instance
  }

  /**
   * element
   * @param type element
   * @return element
   */
  abstract getDefaultOption(type?: string): object;

}