import BaseChart from '../abstractChart'
import OptionAdapter from '../adapterOption/adapter/pie'
import GuiAdapter from '../adapterGui/adapter/basePie'
import DataTransfer from '../../common/dataTransfer/echarts/index'

/**
 * 
 */
export default abstract class BasePieModel extends BaseChart {

  public guiAdapter: any;
  public initialOption: any;
  public optionGroup: any
  public guiOption: any;
  public defaultOption: any;
  public staticData: any;

  public initChartOption() {
    const adapter = new OptionAdapter();
    const initParam = super.getChartParams();
    this.initialOption = adapter.init(this.defaultOption);
    this.optionGroup = adapter.getInstance();

    super.initChart(initParam);
    super.setChartOption(this.initialOption);
  }

  public initChartGui() {
    const adapter = new GuiAdapter();
    this.guiOption = adapter.init(this.initialOption, 'basePie');
    this.guiAdapter = adapter;
  }

  public updateShapeStyle(formObj: any) {
    // id
    const guiId = formObj.rootId;
    const propKey: string = formObj.mKey;

    // 
    if (guiId === 'series') {
      const dataIndex = formObj.index || 0;
      const seriesData = this.shapeStyle[propKey][0]['data'];
      const dataCopy = seriesData.map((item: any, index: number) => {
        if (index === dataIndex) {
          item = super.getUpdatedChartOption(item, this.updatedOption[propKey]);
        }
        return item;
      })
      this.shapeStyle[propKey][0]['data'] = dataCopy;
    }

    // 
    if (['label', 'polar', 'angle'].includes(guiId)) {
      // 
      this.shapeStyle[propKey][0] = super.getUpdatedChartOption(
        this.shapeStyle[propKey][0],
        this.updatedOption[propKey]
      );

      const {
        $innerRadius,
        $outerRadius,
        $point,
        $formatter,
        $showFormatterName
      } = this.shapeStyle[propKey][0];
      // formatter
      this.shapeStyle[propKey][0]['label']['formatter'] = this.optionGroup[propKey].setFormatter({
        $point,
        $formatter,
        $showFormatterName
      });
      // 
      this.shapeStyle[propKey][0]['radius'] = [`${$innerRadius}%`, `${$outerRadius}%`];
    }

    // 
    if (!['series', 'label', 'polar', 'angle'].includes(guiId)) {
      this.shapeStyle[propKey] = super.getUpdatedChartOption(
        this.shapeStyle[propKey],
        this.updatedOption[propKey]
      );
    }
  }

  public updateChartOption(formObj: any) {
    const propKey: string = formObj.mKey;
    const currentOption = {
      [propKey]: this.shapeStyle[propKey]
    };
    // seriesy
    const option = propKey === 'series' ? currentOption : this.updatedOption;
    super.setChartOption(option);
  }

  public isNeedComputedYAxis() {
    return false;
  }

  public setChartSeriesOption(defaults: Chart.Option, seriesData: any[]) {
    if (!seriesData.length) {
      return [{ data: [] }];
    }

    // seriesseries[0]['data']
    const series = _.cloneDeep(defaults.series);
    const realSeries = series[0]['data'];
    const dataList = new DataTransfer().getSeriesDataByMap(seriesData);
    const diff = dataList.length - realSeries.length;
    let addIndex = 0;

    // 
    while (diff > 0 && diff > addIndex) {
      realSeries.push({
        value: 0,
        name: '',
        itemStyle: { color: '' }
      });
      addIndex++;
    }

    // 
    if (dataList.length && diff < 0) {
      realSeries.length = dataList.length;
    }

    realSeries.forEach((item: any, index: number) => {
      const itemColor = super.getChartSeriesColor(index);
      item.name = dataList[index]['name'];
      item.value = dataList[index]['value'];
      item.itemStyle.color = item.itemStyle.color ? item.itemStyle.color : itemColor;
    });
    return series;
  }

  public setChartLegendName() {
    return false
  }

}