import RubberBandCood from './rubberBandCood';
/**
 * 
 */
export default class RubberBand {

    /**
     * 
     */
    public rubberBandCood: RubberBandCood;

    constructor() {
        this.rubberBandCood = new RubberBandCood();
    }

    /**
     * rubberband
     */
    public mapRubberBand( rubberBand ): void {
        this.rubberBandCood.mapRubberBandCood( rubberBand );
    }
    
    public destroyed(): void {
        this.rubberBandCood = null;
    }
}

