import PartStyle from './portion/partStyle';
import RefLine from './portion/refLine';
import { AbstractPart } from '../abstract/abstractPart';

/**
 * partpart
 * elementgroup
 */
export default class Part extends AbstractPart {
    /**
     * 
     */
    public partName: string = '';

    /**
     * id
     */
    public partId: string = '';

    /**
     * partcomponent template
     */
    public partType: string = '';

    /**
     * 
     */
    public sourceType: string = '';

    /**
     * 
     */
    public type: string = '';

    /**
     * 
     */
    public childType: string = '';

    /**
     * 
     */
    public layer: number = 0;

    /**
     * 
     */
    public isLocked: boolean = false;

    /**
     * 
     */
    public partStyle: PartStyle;

    /**
     * 
     */
    public refLine: RefLine;

    /**
     * layer
     */
    public isSelected: boolean;
    public isEdit: boolean;

    constructor(partType: Model.Part) {
        super();
        const {
            partId,
            sourceType,
            type,
            childType,
            partStyle,
            refLine,
            layer,
            isLocked = false,
            partName = '',
        } = partType;
        this.partName = partName;
        this.partId = partId;
        this.sourceType = sourceType;
        this.type = type;
        this.childType = childType;
        this.layer = layer;
        this.isLocked = isLocked;
        this.partStyle = new PartStyle(partStyle);
        this.refLine = new RefLine(refLine);
        this.isSelected = false;
        this.isEdit = false;
    }

    /**
     * 
     * 
     */
    public getPartAllProps(): Model.Part {
        return {
            partName: this.partName,
            partId: this.partId,
            partType: this.partType,
            sourceType: this.sourceType,
            type: this.type,
            childType: this.childType,
            layer: this.layer,
            isLocked: this.isLocked,
            partStyle: this.partStyle.getPartAllProps(),
            refLine: this.refLine.getPartAllProps(),
        };
    }

    /**
     * 
     * 
     */
    public setPartAllProps(partType: Model.Part) {
        const {
            partId,
            sourceType,
            type,
            childType,
            partStyle,
            refLine,
            layer,
            isLocked
        } = partType;

        this.partId = partId;
        this.sourceType = sourceType;
        this.type = type;
        this.childType = childType;
        this.layer = layer;
        this.isLocked = isLocked || false;
        this.partStyle.setPartAllProps(partStyle);
        this.refLine.setPartAllProps(refLine);
    }

    // TODO: 
    public init(domId: string): void { }

    public getSavePartProps() {
        throw new Error('Subclasses should overwrite this method');
    }

    // TODO: 
    public setPublicStyle(option: any): void { }


    public setPartEvent(): void {
        throw new Error('Subclasses should overwrite this method');
    }

    public setPartAnimation(): void {
        throw new Error('Subclasses should overwrite this method');
    }

    // TODO: 
    public destroyed(): void { }

}
