/**
 * store for part relative to pageManage.
 * part
 */
function getCurrentPart(state: Screen.state): any | void {
    const selectGroup = state.editor.partSelection.selectGroup;
    const { length } = selectGroup;
    if (length === 1) {
        return selectGroup[0];
    }
    return null;
}
export default {
    // partshapechart
    set_partStyle(state: Screen.state, option: any) {
        // part
        const currentPart = getCurrentPart(state);
        if (currentPart) {
            currentPart.setPartStyle(option);
        }
    },
    // partshapechart
    set_partData(state: Screen.state, option: any) {
        // part
        const currentPart = getCurrentPart(state);
        if (currentPart) {
            currentPart.setPartData(option);
        }
    },
    // partreportschart
    set_partGuiSeries(state: Screen.state, seriesObj: any) {
        const currentPart = getCurrentPart(state);
        const { type, series } = seriesObj;
        if (currentPart) {
            currentPart.setPartGuiSeries(type, series);
        }
    },
    // partreportschart
    set_partStaticData(state: Screen.state, option: any) {
        const currentPart = getCurrentPart(state);
        if (currentPart) {
            currentPart.setPartStaticData(option);
        }
    }
};
