import BaseChart from '../abstractChart'
import OptionAdapter from '../adapterOption/adapter/lineAndBar'
import GuiAdapter from '../adapterGui/adapter/line'
import DataTransfer from '../../common/dataTransfer/echarts/index'

/**
 * 
 * y: serieszAxisy
 */
export default abstract class BaseLineModel extends BaseChart {

  public guiAdapter: any;
  public initialOption: any;
  public optionGroup: any
  public guiOption: any;
  public defaultOption: any;
  public staticData: any;

  /**
   * 
   */
  abstract isInitArea(): boolean;

  public initChartOption() {
    const adapter = new OptionAdapter();
    const initParam = super.getChartParams();
    this.initialOption = adapter.init(this.defaultOption, 'baseLine');
    this.optionGroup = adapter.getInstance();

    super.initChart(initParam);
    super.setChartOption(this.initialOption);
  }

  public initChartGui() {
    const adapter = new GuiAdapter();
    this.guiOption = adapter.init(this.initialOption, 'baseLine');
    this.guiAdapter = adapter;
  }

  public updateShapeStyle(formObj: any) {
    // id
    const guiId = formObj.rootId;
    const propKey: string = formObj.mKey;

    if (guiId === 'series') {
      const seriesIndex = formObj.index || 0;
      const seriesCopy = this.shapeStyle[propKey]
        .map((item: any, index: number) => {
          if (index === seriesIndex) {
            item = super.getUpdatedChartOption(item, this.updatedOption[propKey])
          }
          return item
        });
      this.shapeStyle[propKey].length = 0;
      this.shapeStyle[propKey] = seriesCopy;
    } else {
      this.shapeStyle[propKey] = super.getUpdatedChartOption(
        this.shapeStyle[propKey],
        this.updatedOption[propKey]
      );
    }
  }

  public updateChartOption(formObj: any) {
    const propKey: string = formObj.mKey;
    const currentOption = {
      [propKey]: this.shapeStyle[propKey]
    };
    // seriesxy
    const option = propKey === 'series' || 'xAxis' ? currentOption : this.updatedOption;
    super.setChartOption(option);
  }

  public isNeedComputedYAxis() {
    return false;
  }

  public setChartSeriesOption(defaults: Chart.Option, seriesData: any[]) {
    if (!seriesData.length) {
      return [];
    }

    const normalItem = {
      label: { color: '' },
      lineStyle: { color: '' },
      itemStyle: { color: '' }
    };
    const areaItem = {
      label: { color: '' },
      lineStyle: { color: '' },
      itemStyle: { color: '' },
      areaStyle: { color: '' }
    };
    const seriesItem = this.isInitArea() ? areaItem : normalItem;
    const series = _.cloneDeep(defaults.series);
    const dataList = new DataTransfer().getPublicSeiresData(seriesData);
    const diff = dataList.length - series.length;
    let addIndex = 0;

    // 
    while (diff > 0 && diff > addIndex) {
      series.push(seriesItem);
      addIndex++;
    }

    // 
    if (dataList.length && diff < 0) {
      series.length = dataList.length;
    }

    series.forEach((item: any, index: number) => {
      if (this.isInitArea()) {
        const areaColor = super.getChartSeriesColor(index, true);
        item.areaStyle.color = item.areaStyle.color ? item.areaStyle.color : areaColor;
      }

      const labelColor = super.getChartSeriesColor(index);
      item.name = item.name ? item.name : `${index + 1}`;
      item.label.color = item.label.color ? item.label.color : labelColor;
      item.lineStyle.color = item.lineStyle.color ? item.lineStyle.color : labelColor;
      item.itemStyle.color = item.itemStyle.color ? item.itemStyle.color : labelColor;
      item.data = dataList[index];
    });
    return series;
  }

  public setChartLegendName() {
    return false
  }

}