import {
  transferDataByDimens,
  groupDataByDimens,
  getStackDataByDimens
} from './util';

/**
 *  
 */
export default class DataTransfer {

  /**
   * 
   * @param data 
   * @param reverse xy[x, y]
   */
  public getPublicSeiresData(data: Model.DataFormatA[], reverse?: boolean): Array<any[]> {
    return transferDataByDimens(data, reverse);
  }

  /**
   * y
   * @param yAxisIndex 0y11y2
   */
  public getBothAxisSeiresData(data: Model.DataFormatB[], yAxisIndex: 0 | 1, reverse?: boolean): Array<any[]> {
    const yAxisIndexData = groupDataByDimens(data, 'z')[yAxisIndex];
    return this.getPublicSeiresData(yAxisIndexData, reverse);
  }

  /**
   * 
   */
  public getStackSeriesData(data: Model.DataFormatA[], reverse?: boolean): Array<any[]> {
    return getStackDataByDimens(data, reverse);
  }

  /**
   * 
   * 
   */
  public getSingleSeriesData(data: Model.DataFormatC[], reverse?: boolean): Array<any[]> {
    const dataList: Array<any[]> = [[]];
    for (let item of data) {
      // xy
      const { x = '', y = '' } = item;
      const data: any[] = reverse ? [y, x] : [x, y];
      dataList[0].push(data);
    }
    return dataList;
  }

  /**
   * 
   * 
   */
  public getStackSingleSeriesData(data: Model.DataFormatC[], reverse?: boolean): Array<any[]> {
    const stackData: Array<any[]> = [];
    const stackValue = data
      .filter((item: any) => !Number.isNaN(Number(item.y)))
      .map((item: any) => Math.round(item.y));
    const max = stackValue.length ? Math.max.apply(null, stackValue) : 0;

    for (let item of data) {
      const { x = '' } = item;
      const res = reverse ? [max, x] : [x, max];
      stackData.push(res)
    }
    return stackData;
  }

  /**
   * 
   * 
   * @param map namexvaluey
   */
  public getSeriesDataByMap(data: Model.DataFormatC[], map?: Model.MapRule) {
    const seriesList: any[] = [];
    const { _x = 'name', _y = 'value' } = map || {};

    for (let item of data) {
      const { x = '', y = 0 } = item;
      const series = {
        [_x]: x,
        [_y]: Number.isNaN(Number(y)) ? 0 : Number(y)
      };
      seriesList.push(series)
    }
    return seriesList;
  }

}