import { PartService }  from 'storage/data/part.service';
export default class PureModelMapper {

    public editor: any;

    public pageManage: any;

    public project: any;

    public cmptManage: any;

    constructor( model ) {
        const { editor, pageManage, projectObj, cmptManage } = model;
        this.editor = editor;
        this.pageManage = pageManage;
        this.project = projectObj;
        this.cmptManage = cmptManage;
    }
    /**
     * 
     */
    public getHisPureModel() {
        return {
            editor: this.editor.mapEditorAllProps(),
            partGroup: this.pageManage.mapPartAllProps(),
        };
    }
    /**
     * 
     */
    public setHisPureModel( pureModel ): void {
        // bind typeof pageManage.findPartGroup
        const findPartFuc = this.pageManage.findPartGroup.bind(this.pageManage);
        const { editor, partGroup } = pureModel;
        this.pageManage.resetPartAllProps(partGroup);
        this.editor.resetEditorAllProps(editor, findPartFuc);
    }

    /**
     * 
     */
    public getSaveCmptPureModel(): Model.CmptPureData {
        // 
        const cmptPureData = this.cmptManage.getPureData();
        return {
            cmptPureData,
        };
    }

    /**
     * 
     */
    public getSavePureModel(): Model.ProjectPureData {
        // 
        const pagePureData = this.pageManage.mapPagesAllProps();
        // 
        // const cmptPureData = this.cmptManage.getPureData();
        // 
        
        // 
        const projectPureData = this.project.getPureData();

        const data = PartService.saveProjectDetail({
            pagePureData,
            // cmptPureData,
            projectPureData,
        });
        return data;
    }

    /**
     * [  ] - 
     */
    public setSavePureModel( pureData: any ) {
        this.pageManage.resetPagesAllProps(pureData.pagePureData);
        this.cmptManage.setPureData(pureData.cmptPureData);
        this.project.setPureData(pureData.projectPureData);
    }

    /**
     * [  ] - 
     */
    public setPreSavePureModel( pureData: any ) {
        this.pageManage.resetPagesAllProps(pureData.pagePureData);
        this.project.setPureData(pureData.projectPureData);
    }

     /**
     * 
     */
    public destrory() {
        this.editor = null;
        this.pageManage = null;
        this.project = null;
        this.cmptManage = null;
    }
}
