
import Project from 'model/project/project';
export default class Builder {

    /**
     * project
     * @mode 
     * @dataMap 
     * @returns project
     */
    public create(mode: string, dataMap: any) {
        const project = new Project();

        if (mode === 'edit') {
            project.init();
            if ( this.isEmpty(dataMap) ) {
                project.pureModelMapper.setSavePureModel(dataMap);
            }
        } else if (mode === 'preview') {
            project.initPreview();
            if ( this.isEmpty(dataMap) ) {
                project.pureModelMapper.setPreSavePureModel(dataMap);
            }
        }

        return project;
    }

    private isEmpty(dataMap: any): boolean {
        if (!_.isNil(dataMap) && !_.isEmpty(dataMap) ) {
            return true;
        }
        return false;
    }
    
}
