import HoverPartObject from './hoverPartObject';

/**
 * 
 */
export default class HoverPart {

    public isHoverPart: boolean = false;

    public partStyle: any = new HoverPartObject();

    /**
     * type
     */
    public setIsHoverPart( isHoverPart ): void {
        this.isHoverPart = isHoverPart;
    }

    /**
     * partStyle
     */
    public mapHoverPartStyle( partStyle ): void {
        this.partStyle.mapHoverPartObject( partStyle );
    }

    public destroyed(): void {
        this.isHoverPart = null;
        this.partStyle = null;
    }
}
