// 
import global from '../config/public/global';
import legend from '../config/public/legend';
import title from '../config/public/title';
import tooltip from '../config/public/tooltip';
// 
import xAxisVerticalBase from '../config/bar/xAxisVerticalBase';
import yAxisVerticalGroup from '../config/bar/yAxisVerticalGroup';
import zAxisBase from '../config/bar/zAxisBase';
import barBaseLineBar from '../config/bar/barBaseLineBar';
import seriesBaseBar from '../config/bar/seriesBaseBar';
import seriesBaseLine from '../config/bar/seriesBaseLine';
// 
import BaseGuiAdapter from '../../../common/adapter/baseGuiAdapter';

/**
 * 
 */
export default class BaseLineBar extends BaseGuiAdapter {

  public seriesLength: number;

  /**
   * series.typebarlength
   */
  public barSeriesLength: number;

  /**
   * series.typelinelength
   */
  public lineSeriesLength: number;

  public getDefaultOption(type: string): any {
    const map: any = {
      baseLineBar: {
        global,
        title,
        tooltip,
        legend,
        barBaseLineBar,
        xAxis: xAxisVerticalBase,
        yAxis: yAxisVerticalGroup,
        zAxis: zAxisBase,
        seriesBar: seriesBaseBar,
        seriesLine: seriesBaseLine
      }
    };
    return map[type] || {};
  }

  public createGuiGroup(guiObj: object): any[] {
    if (!_.isPlainObject(guiObj)) {
      return [];
    }

    const guiOption: any[] = [];
    let seriesBar: any = null;
    let seriesLine: any = null;

    for (let [key, value] of Object.entries(guiObj)) {
      if (key === 'seriesBar') {
        seriesBar = this.createSeriesGui(value, 'bar');
      } else if (key === 'seriesLine') {
        seriesLine = this.createSeriesGui(value, 'line')
      } else {
        guiOption.push(value);
      }
    }

    // 
    const seriesLineChildren = seriesLine.children || [];
    seriesBar.children.push(...seriesLineChildren);
    guiOption.push(seriesBar);
    return guiOption;
  }

  public createSeriesGui(defaultSeries: any, type: string): Object {
    const series = defaultSeries.children[0] || {};
    const seriesMap = [];
    const length = type === 'bar' ? this.barSeriesLength : this.lineSeriesLength;
    const seriesName = type === 'bar' ? '' : '';

    for (let i = 0; i < length; i++) {
      // series
      const item = _.cloneDeep(series)
      seriesMap.push(item)
    }

    seriesMap.forEach((each, index) => {
      // seriesseries.type
      // typebarn,line series.length - n 
      const seriesIndex = type === 'bar' ? index : this.barSeriesLength + index;
      each.name = `${seriesName}${index + 1}`;
      // 
      each.index = seriesIndex
      this.createSeriesIndex(each, seriesIndex);
    })

    defaultSeries.children.length = 0;
    defaultSeries.children = seriesMap;
    return defaultSeries;
  }

  public createSeriesIndex(item: any, index: number) {
    const hasChild = item.children.length;
    if (!hasChild) {
      item.component.index = index;
    } else {
      const children = item.children;
      for (let item of children) {
        this.createSeriesIndex(item, index);
      }
    }
  }

  public initGuiOption(guiOption: any[], defaultStyle: any): any[] {
    if (!_.isArray(guiOption) || _.isEmpty(guiOption) || !_.isPlainObject(defaultStyle)) {
      return [];
    }

    const _guiOption: any = _.cloneDeep(guiOption);
    for (let item of _guiOption) {
      const guiId = item.id;
      if (guiId === 'series') {
        // :series
        // typebarn,line series.length - n 
        const value = defaultStyle.series || [];
        item.children.forEach((ele: any, index: number) => {
          super.assignSeriesGuiValueDeep(ele, value[index]);
        })
      }

      if (guiId === 'barStyle') {
        // barCategoryGapbarGap
        // barCategoryGapbarGap
        // baradapterseries-lineseries-bar,
        // series-line
        const key = 'series';
        const value = defaultStyle.series[0] || [];
        super.assignPublicGuiValueDeep(key, value, item.children);
      }

      if (!['series', 'barStyle'].includes(guiId)) {
        // : xyz
        for (let [key, value] of Object.entries(defaultStyle)) {
          super.assignPublicGuiValueDeep(key, value, item.children);
        }
      }
    }

    return _guiOption;
  }

  public updateGuiOption(guiOption: any[], currentStyle: any, opts: any): any[] {
    const guiId = opts.rootId;
    const key = opts.mKey;
    const currentOption = guiOption.find((item: any) => item.id === guiId);

    // 
    // series
    let value: any = null;
    if (guiId === 'series') {
      value = currentStyle.series || [];
      currentOption.children.forEach((ele: any, index: number) => {
        super.assignSeriesGuiValueDeep(ele, value[index]);
      });
    }

    if (guiId === 'barStyle') {
      value = currentStyle.series[0] || [];
      super.assignPublicGuiValueDeep(key, value, currentOption.children);
    }

    if (!['series', 'barStyle'].includes(guiId)) {
      value = currentStyle[key];
      super.assignPublicGuiValueDeep(key, value, currentOption.children);
    }

    return guiOption;
  }

  /**
   * 
   * @param seriesGui 
   * @param seriesStyle shapeStyle.series
   */
  public updateSeriesGuiOption(seriesGui: any, seriesStyle: any) {
    const optionList = seriesGui['children'] || []

    // 
    optionList.forEach((each: any, index: number) => {
      each.name = `${index + 1}`
      // 
      each.index = index
      // 
      each.editable = true
      this.createSeriesIndex(each, index)
    })

    // 
    optionList.forEach((ele: any, index: number) => {
      super.assignSeriesGuiValueDeep(ele, seriesStyle[index])
    })
  }

  public init(defaultObj: any, type: string): any[] {
    this.barSeriesLength = (defaultObj.series || [])
      .filter((item: any) => item.type === 'bar').length;
    this.lineSeriesLength = (defaultObj.series || [])
      .filter((item: any) => item.type === 'line').length;

    const defaultOption = this.getDefaultOption(type);
    const guiList = this.createGuiGroup(defaultOption);
    const guiOption = this.initGuiOption(guiList, defaultObj);
    return guiOption;
  }

}