// Object.prototype
const has = Object.prototype.hasOwnProperty;

/**
 * 
 * 
 * @param target {object} 
 * @param init {object} 
 * @returns {object} 
 */
const updatePropsDeep = function updateObjPropsDeep(
  target: ObjProps.Key,
  init: ObjProps.Key,
): object {
  if (typeof init !== 'object') {
    return target;
  }

  const cloneObj: ObjProps.Key = {};
  let cloneInit: ObjProps.Key = init;
  if (target && typeof target === 'object') {
    for (let key in target) {
      if (has.call(target, key)) {
        if (target[key] && typeof target[key] === 'object') {
          // Array
          if (Array.isArray(target[key])) {
            cloneObj[key] = has.call(init, key) ? init[key] : target[key];
          } else {
            cloneInit = init[key] || {};
            cloneObj[key] = updateObjPropsDeep(target[key], cloneInit);
          }
        } else {
          cloneObj[key] = has.call(init, key) ? init[key] : target[key];
        }
      }
    }
  }
  return cloneObj;
};

export default updatePropsDeep;
