import echarts from 'echarts';
import { Element } from 'model/element/element';
import updatePropsDeep from 'utils/initProps';
import Shape from '@/views/screen/libary/echarts';

/**
 * Echarts
 */
export default abstract class BaseChart extends Element {

  /**
   * Echarts
   */
  public chartInstance: any;

  /**
   * 
   */
  public updatedOption: any;

  /**
   * 
   */
  public optionAdapter: any;

  /**
   * 
   */
  abstract guiAdapter: any;

  /**
   * 
   */
  abstract initialOption: any;

  /**
   * 
   * gridtooltiplegend...
   */
  abstract optionGroup: any

  /**
   * 
   */
  abstract guiOption: any;

  /**
   * 
   */
  abstract defaultOption: any;

  /**
   * 
   */
  abstract staticData: any;

  constructor(part: Model.Part) {
    super(part);
  }

  /**
   * 
   * @param options InitParam
   */
  public initChart(options: Chart.InitParam): void {
    const { dom, theme, opts } = options;
    this.chartInstance = echarts.init(dom, theme, opts);
  }

  /**
   * 
   * @param option 
   * @param notMerge  option false
   */
  public setChartOption(option: any, notMerge?: boolean): void {
    this.chartInstance.setOption(option, notMerge);
  }

  /**
   * 
   * @param w 
   * @param h 
   */
  public resize(w: number, h: number): void {
    this.chartInstance.resize({ width: w, height: h });
  }

  /**
   * 
   * @param eventType 
   * @param fn 
   */
  public setChartEvent(eventType: string, cb: (params?: any) => void): void {
    this.chartInstance.on(eventType, cb);
  }

  /**
   * 
   */
  public clear(): void {
    this.chartInstance.clear();
  }

  /**
   * 
   * @param themeName 
   * @param theme 
   */
  public getRegisterTheme(themeName: string, theme?: Object): string {
    const defaultTheme = ['light', 'dark', 'default'];
    if (defaultTheme.includes(themeName)) {
      return themeName;
    } else {
      const themeObj = theme || {};
      echarts.registerTheme(themeName, themeObj);
      return themeName;
    }
  }

  /**
   * 
   * @param seriesIndex index0 ~ n
   * @param notArea 
   */
  public getChartSeriesColor(seriesIndex: number, notArea?: boolean): string {
    const lightColor = notArea ?
      [
        'rgb(0,140,255,0.35)',
        'rgb(42,255,191,0.35)',
        'rgb(255,227,76,0.35)',
        'rgb(255,111,0,0.35)',
        'rgb(255,66,173,0.35)'
      ] :
      [
        '#008cff',
        '#2affbf',
        '#ffe34c',
        '#ff6f00',
        '#ff42ad'
      ];
    const index = seriesIndex % lightColor.length;
    return lightColor[index];
  }

  /**
   * 
   */
  public getChartInitOption(defaults: Chart.Option, seriesData: any[]) {
    const series = this.setChartSeriesOption(defaults, seriesData);
    defaults.series.length = 0;
    defaults.series = series;

    // legend.data
    this.setChartLegendName(defaults);
    this.defaultOption = defaults;
  }

  /**
   * 
   * 
   * 
   * @param defaults ()shapeStyle()
   * @param seriesData 
   */
  abstract setChartSeriesOption(defaults: Chart.Option, seriesData: any[]): Chart.SeriesItem[];

  /**
   * 
   * legend.data
   */
  abstract setChartLegendName(defaults?: Chart.Option): void;

  /**
   * :y
   * y
   */
  abstract isNeedComputedYAxis(): boolean;

  /**
   * 
   */
  public getChartParams(): Chart.InitParam {
    const theme = this.getRegisterTheme('light');
    return {
      dom: document.getElementById(this.partId),
      theme,
      opts: {
        devicePixelRatio: window.devicePixelRatio,
        width: this.partStyle.width,
        height: this.partStyle.height
      }
    }
  }

  /**
   * 
   * @param origin 
   * @param option 
   * @returns {object} 
   */
  public getUpdatedChartOption(origin: any, option: any) {
    return updatePropsDeep(origin, option);
  }

  /**
   * 
   */
  public initChartModel(): void {
    this.initChartOption();
    this.initChartGui();
    this.initChartShapeStyle();
  }

  /**
   * shapeStyle
   */
  public initChartShapeStyle(): void {
    this.shapeStyle = { ...this.initialOption };
  };

  /**
   * 
   * @param formObj 
   */
  public updateChartModel(formObj: any): void {
    this.setUpdatedChartOption(formObj);
    this.updateShapeStyle(formObj);
    this.updateChartOption(formObj);
    this.updateChartGui(formObj);
  }

  /**
   * 
   */
  public setUpdatedChartOption(formObj: any): void {
    // : 'gird' 'xAxis'
    const propKey: string = formObj.mKey;
    // : 'grid.left'
    const propPath: string = formObj.path;
    // 
    const propValue: any = formObj.val;
    // 
    const isGroup = formObj.isGroup || false;

    /**
     * 
     * gird: {
     *    left: 80 
     * }
    */
    const updateProps: any = _.zipObjectDeep([propPath], [propValue]);

    // 
    if (!isGroup) {
      this.updatedOption = updateProps;
    } else {
      // key-value
      const groupKey: string = propPath.split('.').pop();
      const groupValue = _.get(updateProps, propPath);
      // 
      const groupProp = this.optionGroup[propKey].computed(groupValue, groupKey);
      const merge = { ...groupProp, ...updateProps[propKey] };
      this.updatedOption = { [propKey]: merge };
    }
  }

  /**
   * shapeStyle
   */
  public updateChartGui(formObj: any): void {
    const propKey: string = formObj.mKey;
    const currentOption = {
      [propKey]: this.shapeStyle[propKey]
    };
    this.guiOption = this.guiAdapter.updateGuiOption(
      this.guiOption,
      currentOption,
      formObj,
    );
  }

  /**
   * 
   * 
   * 1)initialOption
   * 2)
   */
  abstract initChartOption(): void;

  /**
   * 
   * 
   * 1)guiAdapter
   * 2)guiOption
   */
  abstract initChartGui(): void;

  /**
   * shapeStyle
   * 
   * shapeStyle
   */
  abstract updateShapeStyle(formObj: any): void;

  /**
   * 
   * 
   * shapeStyle
   */
  abstract updateChartOption(formObj: any): void;

  /**
   * 
   * @param domId dom id
   */
  public init(domId: string): void {
    if (_.isNil(domId) && !(<string>domId).includes('#')) {
      return;
    }

    // model  application 
    this.instance = new Shape({});
    // 
    this.initChartModel();
  }

  /**
   * 
   */
  public setPublicStyle(opts: Model.PartStyle): void {
    if (!_.isObject(opts)) {
      return
    }

    if (opts.width) {
      this.resize(opts.width, null)
    }
    if (opts.height) {
      this.resize(null, opts.height)
    }
    // partStyle
    for (let [key, value] of Object.entries(opts)) {
      if (!_.isNil(this.partStyle[key])) {
        this.partStyle[key] = value
      }
    }
  }

  /**
   * 
   */
  public setPartStyle(opts: any): void {
    if (!_.isObject(opts)) {
      return;
    }

    // 
    // 
    // TODO
    // const guiIndex = opts.index;
    // const seriesIndex = this.shapeStyle['series'].length - 1;
    // if (guiIndex > seriesIndex) {
    //   return;
    // }

    // shapeStyle
    this.updateChartModel(opts);
  }

  /**
   * 
   * @param setType : add | delete
   * @param seriesOption 
   */
  public setPartGuiSeries(setType: string, seriesOption: any) {
    if (setType === 'add') { this.addGuiSeriesByPublic() }
    if (setType === 'delete') { this.delGuiSeriesByPublic(seriesOption) }
  }

  /**
   * 
   */
  public addGuiSeriesByPublic() {
    const seriesGui = this.guiOption.find((item: any) => item.id === 'series');
    // 
    const copyItem = _.cloneDeep(seriesGui.children[0]);
    seriesGui.children.push(copyItem);

    // 
    this.guiAdapter.updateSeriesGuiOption(seriesGui, this.shapeStyle.series);
  }

  /**
   * 
   */
  public delGuiSeriesByPublic(seriesOption: any) {
    const seriesGui = this.guiOption.find((item: any) => item.id === 'series');
    const len = seriesGui.children.length;
    if (len >= 2) {
      // 
      const delIndex = seriesOption.index;
      const currIndex = seriesGui.children.findIndex((item: any, index: number) => index === delIndex);
      seriesGui.children.splice(currIndex, 1);

      // 
      this.guiAdapter.updateSeriesGuiOption(seriesGui, this.shapeStyle.series);

      // 
      if (len === 2) {
        seriesGui.children.forEach((item: any) => {
          item.editable = false;
        });
      }
    }
  }

  /**
   * 
   * @param seriesData 
   */
  public setChartOptionByData(seriesData: any[]) {
    const series = this.setChartSeriesOption(this.shapeStyle, seriesData);
    // series
    const initSeries = this.optionGroup.series.init(series);
    // shapeStyle
    this.shapeStyle.series.length = 0;
    this.shapeStyle.series = initSeries;
    // legend.data
    this.setChartLegendName(this.shapeStyle);

    // 
    if (this.isNeedComputedYAxis()) {
      const opts = this.optionAdapter.computedYAxis(this.shapeStyle);
      this.clear();
      this.setChartOption(opts);
    } else {
      this.clear();
      this.setChartOption(this.shapeStyle);
    }
  }

  /**
   * 
   */
  public getPartStaticData(): any[] {
    console.log('')
    return this.staticData;
  }

  /**
   * part
   */
  public getPartAllProps(): Model.Part {
    const parentProps = super.getPartAllProps();
    return {
      ...parentProps,
      shapeStyle: this.shapeStyle,
      staticData: this.staticData
    }
  }

  /**
   * 
   * @param option 
   */
  public setPartStaticData(option: any): void {
    console.log('');
    if (option.length > 0) {
      this.staticData = option;
      this.setChartOptionByData(option);

      // TODO:
      // const seriesGui = this.guiOption.find((item: any) => item.id === 'series');
      // this.guiAdapter.updateSeriesGuiOption(seriesGui, this.shapeStyle['series']);
    }
  }

  /**
   * 
   * 
   * @param option 
   */
  public setPartActiveData(option: any): void {
    if (option.length > 0) {
      this.setChartOptionByData(option);
    }
  }

  /**
   * 
   */
  public destroyed(): void {
    this.clear();
  }

}