export default class Cmpt implements Model.Cmpt {
    /**
     * base64cdnblob
     */
    public imgSrc: string = '';

    public cmptId: string = '';

    public cmptGroupId: string = '';

    public cmptName: string = '';

    public isEditor: boolean;

    /**
     * group,
     */
    public partSource: any = {};

    /**
     * 
     */
    public projectId: string = '';

    constructor(cmptArgs: Model.Cmpt) {
        const {
            imgSrc,
            cmptId,
            cmptName,
            partSource,
            projectId,
            cmptGroupId,
            isEditor,
        } = cmptArgs;

        this.imgSrc = imgSrc || '';
        this.cmptId = cmptId || '';
        this.cmptGroupId = cmptGroupId || '';
        this.cmptName = cmptName || '';
        this.partSource = partSource || [];
        this.projectId = projectId || '';
        this.isEditor = isEditor || false;
    }
}
