









import Vue from 'vue';
import ctmMixins from './ctmMixins';
import { mapMutations, mapState } from 'vuex';
import CtmItem from './ctmItem.vue';

export default Vue.extend({
    name: 'canvasCtm',
    mixins: [ctmMixins],
    props: {
        mousePos: {
            type: Object,
            default: () => {
                return {
                    x: 0,
                    y: 0
                };
            },
        },
    },
    components: {
        CtmItem,
    },
    computed: {
        ...mapState({
            basePartList: (state: any) => state.screen.pageManage.partContainer,
        }),
    },
    methods: {
        ...mapMutations([
            'lockPart',
            'handleLayer',
            'add_cmpt',
            'setSelectedBox',
        ]),
        /**
         * keyCode
         *  - 
         *  - 
         */
        initFormatCtm() {
            const unexpectList = [
                {
                    perent: 'clipboard',
                    child: ['copy', 'cut', 'delete'],
                },
                {
                    perent: 'lock',
                },
                {
                    perent: 'group',
                },
                {
                    perent: 'history',
                },
                {
                    perent: 'layer',
                },
                {
                    perent: 'lib',
                },
                {
                    perent: 'scale',
                }
            ];
            this.initFormatCtmConfig(unexpectList);
        },
        itemClickDown(item, showCtm) {
            if (!showCtm) {
               this.$emit('handleShowCtm', showCtm);
            }
        },
        // ...
        editorEvtEmitter(evtName: string) {
            this.$root.busRef.$emit(evtName, evtName);
        },
        /**
         * item 
         */
        itemClickBroadcast(item, showCtm, event): void {
            if (!showCtm) {
                this.$emit('handleShowCtm', showCtm);
                this.setSelectedBox(false);
            }
            this.editorEvtEmitter(item.type);
        },
        /**
         * 
         */
        onMenuChange() {
            this.$root.busRef.$on('enableWatch', (message) => {
                if (_.isNil(message)) {
                    return;
                }
                if (!Array.isArray(message)) {
                    message = [message];
                }
                const copyMsg = _.cloneDeep(message);
                copyMsg.forEach((emit: any) => {
                    this.canDisableMenu(emit);
                });
            });
        },
        /**
         * 
         */
        canDisableMenu(enableObj: { parent: string, child: string, enable: boolean }) {
            // layer
            const expectedItem = ['paste'];
            if (!expectedItem.includes(enableObj.child)) {
                return;
            }

            // falsetrue
            const canDo = !enableObj.enable;
            const updateCtmCfj = (ctmCfgList: any, ctmIndex: number) => {
                // 
                if (ctmCfgList[ctmIndex].disable === canDo) {
                    return;
                }
                const ctmItem: any = _.cloneDeep(ctmCfgList[ctmIndex]);
                ctmItem.disable = canDo;
                ctmCfgList.splice(ctmIndex, 1, ctmItem);
            };

            // 
            this.ctmConfig.some((item: any) => {
                const secondItemList = item.value;
                secondItemList.map((chidItem: any, secondIndex: number) => {
                    updateCtmCfj(secondItemList, secondIndex);
                });
            });
        },
    },
    mounted() {
        this.initFormatCtm();
        this.onMenuChange();
    },
    beforeDestroy() {
        this.$root.busRef.$off('enableWatch');
    },
});
