/**
 * element
 * 
 */
export default abstract class Adapter {

  /**
   * elementshapeStyleseries.length
   */
  abstract seriesLength: number;

  /**
   * element
   * @param type element
   * @return element
   */
  abstract getDefaultOption(type: string): object;

  /**
   * element
   * @param guiObj element
   *  {
   *    global: {
   *      level: 0
   *      name: ""
   *      component: null
   *      children: [{}, {}, {}]
   *    }
   *    title:{
   *      ...
   *    }
   *    ...
   *  }
   * @return element
   */
  abstract createGuiGroup(guiObj: object): any[];

  /**
   * elementshapeStyleseries.lengthseries
   * @param defaultSeries series
   * @param length series.type, series.type
   * @return series
   */
  abstract createSeriesGui(defaultSeries: any, type?: string): void;

  /**
   * elementseries
   * @param item series
   * @param index seriesindex
   */
  abstract createSeriesIndex(item: any, index: number): void;

  /**
   * element
   * @param guiOption element
   *  [
   *    {
   *      level: 0
   *      name: ""
   *      component: null
   *      children: [{}, {}, {}]
   *    }
   *    {
   *      level: 0,
   *      name: '',
   *      ...
   *    }
   *    ...
   *  ]
   * @param defaultStyle element(shapeStyle)
   *  {
   *    grid: {
   *      bottom: 60
   *      left: 60
   *      right: 66
   *      top: 80
   *      ...
   *    }
   *    series: [
   *      {
   *        ...
   *      }
   *      ...
   *    ]
   *    ...
   *  }
   * @return element
   */
  abstract initGuiOption(guiOption: any[], defaultStyle: any): any[];

  /**
  * element
  * @param guiOption element
  *  [
  *    {
  *      level: 0
  *      name: ""
  *      component: null
  *      children: [{}, {}, {}]
  *    }
  *    {
  *      level: 0,
  *      name: '',
  *      ...
  *    }
  *    ...
  *  ]
  * @param currentStyle element(shapeStyle)
  *  {
  *    grid: {
  *      bottom: 100
  *      left: 60
  *      right: 60
  *      top: 60
  *      ...
  *    }
  *  }
  * @param formObj 
  *  {
  *    rootId: "legend"
  *    type: "inputNumber"
  *    formOption: {
  *      max: 36
  *      min: 0
  *      step: 0.5
  *    }
  *    mKey: "legend"
  *    path: "legend.textStyle.fontSize"
  *    model: 12.5
  *    val: 12.5
  *  }
  * @return element
  */
  abstract updateGuiOption(guiOption: any[], currentStyle: any, formObj: any): any[];

  /**
   * element
   * @param defaultObj element(shapeStyle)
   * @param type element
   * @return element
   */
  abstract init(defaultObj: any, type: string): any[];

  /**
   * 
   * @param key element(shapeStyle)key
   *  'gird' 'xAxis'
   * @param value element(shapeStyle)value
   *  girdvalue
   *  {
   *     bottom: 100
   *     left: 60
   *     right: 60
   *     top: 60
   *  }
   * @param option element
   */
  public assignPublicGuiValueDeep(key: string, value: any, option: any[]): void {
    for (let item of option) {
      const hasChild = item.children.length
      if (!hasChild) {
        const mKey = item.component.mKey
        if (mKey === key) {
          const path = item.component.path
          const modelPath = path.replace(`${mKey}.`, ``)
          item.component.model = _.get(value, modelPath)
        }
      } else {
        this.assignPublicGuiValueDeep(key, value, item.children)
      }
    }
  }

  /**
   * series
   * @param seriesGui element
   * @param seriesStyle elementseries
   */
  public assignSeriesGuiValueDeep(seriesGui: any, seriesStyle: any): void {
    const option = seriesGui.children
    for (let item of option) {
      const hasChild = item.children.length
      if (!hasChild) {
        const mKey = item.component.mKey
        const path = item.component.path
        const modelPath = path.replace(`${mKey}.`, ``)
        item.component.model = _.get(seriesStyle, modelPath)
      } else {
        this.assignSeriesGuiValueDeep(item, seriesStyle)
      }
    }
  }

}