export default class EventObject {
    
    
    /**
     * nativevue
     */
    public isNative: boolean = true;

    /**
     * 
     */
    public eventName: string = '';

    /**
     * , 
     */
    public isConsumed: boolean = false;

    /**
     * mouseDown,up,move
     */
    public eventType: string  = '';

    /**
     * 
     */
    public enable: boolean = true;

    /**
     * 
     */
    public keyCode: string  = '';

    /**
     * domvue
     */
    public domId: string = '';

    /**
     * vueremovefalsedomfalse
     */
    public active: boolean = true;

    /**
     * 
     */
    public handle: () => void;

    constructor( eventTemp? ){
        if(eventTemp) {
            this.mapEventObject(eventTemp);
        }
    }

    mapEventObject( eventTemp ): void {
        const { 
            isNative = true, 
            eventName = '', 
            isConsumed = false, 
            eventType = '',
            enable = true,
            keyCode = '',
            domId = '',
            active = true
        } = eventTemp; 
        this.isNative = isNative;
        this.eventName = eventName;
        this.isConsumed = isConsumed;
        this.eventType = eventType;
        this.enable = enable;
        this.keyCode = keyCode;
        this.domId = domId;
        this.active = active;
    }

}