
import Shape from '@/views/screen/libary/baseShape';
import { Element } from '../element';
import updatePropsDeep from 'utils/initProps';

/**
 * 
 */
export default abstract class BaseShape extends Element {

  public instance: any;
  public shapeStyle: any;

  /**
   * 
   */
  public defaultOption: any;

  /**
   * 
   */
  abstract guiOption: any;

  /**
   * 
   */
  abstract guiAdapter: any;

  constructor(part: Model.Part) {
    super(part);
  }

  /**
   * 
   * @param opts 
   *  {
   *    background: {
   *      backgroundColor: "#999"
   *    }
   *    border: {
   *      borderColor: "#ccc"
   *      borderStyle: "solid"
   *      borderWidth: 0
   *    }
   *    ...
   *  }
   * @return 
   *  {
   *    backgroundColor: "#999"
   *    borderColor: "#ccc"
   *    borderStyle: "solid"
   *    borderWidth: 0
   *    ...
   *  }
   */
  public mergeOptions(opts: any): object {
    const merge: any = {};
    for (let item of Object.values(opts)) {
      for (let [key, value] of Object.entries(item)) {
        merge[key] = value;
      }
    }
    return merge;
  }

  /**
   * 
   */
  public initShapeModel() {
    this.initShapeOption();
    this.initShapeGui();
  }

  /**
   * 
   * 
   * 1)
   * 2)shapeStyle
   */
  abstract initShapeOption(): void;

  /**
   * 
   * 
   * 1)guiAdapter
   * 2)guiOption
   */
  abstract initShapeGui(): void;

  /**
   * 
   * @param {string} domStr id
   */
  public init(domId: string): void {
    if (_.isNil(domId) && !(domId as string).includes('#')) {
      return;
    }
    this.instance = new Shape();
    this.instance.typeName = this.childType;

    // shape
    this.initShapeModel();
    // shape
    this.instance.option = { content: this.shapeStyle };
    // shape
    this.instance.$mount(domId);
  }

  /**
   * 
   */
  public setPublicStyle(option: Model.PartStyle): void {
    if (!_.isObject(option)) {
      return;
    }

    // shape
    if (option.width) {
      const widthWrap = { content: { width: option.width } };
      this.instance.$children[0].setOption(widthWrap);
    }
    if (option.height) {
      const heightWrap = { content: { height: option.height } };
      this.instance.$children[0].setOption(heightWrap);
    }
    if (option.opacity) {
      const opacityWrap = { content: { opacity: option.opacity } };
      this.instance.$children[0].setOption(opacityWrap);
    }
    // shapepartStyle
    for (let [key, value] of Object.entries(option)) {
      if (!_.isNil(this.partStyle[key])) {
        this.partStyle[key] = value;
      }
    }
  }

  /**
   * 
   */
  public setPartStyle(formObj: any): void {
    const propKey: string = formObj.mKey;
    const propPath: string = formObj.path;
    const propValue: any = formObj.val;
    const updateProps: any = _.zipObjectDeep([propPath], [propValue]);

    // shapeStyle
    this.shapeStyle = updatePropsDeep(this.shapeStyle, updateProps[propKey]);
    // shape
    this.instance.$children[0].setOption(updateProps);
    // 
    this.guiOption = this.guiAdapter.updateGuiOption(this.guiOption, this.shapeStyle, formObj);
  }

  /**
   * part
   */
  public getPartAllProps(): Model.Part {
    const parentProps = super.getPartAllProps();
    return {
      ...parentProps,
      shapeStyle: this.shapeStyle
    }
  }

  public destroyed(): void {
    if (this.instance && !_.isEmpty(this.instance)) {
      this.instance.$destroy();
    }
  }

}
