import Point from './statsPoint/point';
import UUID from 'utils/uuid';

export default class PointModel implements Model.PointModel{
    /**
     * Id
     */
    public sourceTypeId: string;

    /**
     * 
     */
    public sourceType: string = 'point';

    /**
     *  - 
     * 
     * 
    */
    public pointType: string;

    /**
    * 
    * 
    */
    public pointDeviceType: string;

    /**
    * 
    */
    public pointDevice: string;

    /**
    * 
    */
   public pointName: string;


    /**
     *  - [, ]
     */
    public valueType: string; 

    /**
     * 
     * [{x: '', y: 12,s: 1}]
     */
    public pointFormat: string; 

    /**
     *  - [Point, Point]
     */
    public pointGroup: Point[];

    /**
     *  - [ 2019/1/1 - 2019/1/30 ]
     */
    public dateRange: [string, string];

    /**
     *  - [ 2:00 - 16:00 ]
     */
    public timeRange: [string, string];

    /**
     *  ws/polling
     *  - api + ws
     *  - api + ws
     * 
     * 
     * TMapi+ws
    */
    public reqMethod: string;

    constructor(pointData: any) {
        const {
            pointType,
            pointDeviceType,
            pointDevice,
            pointName,

            valueType,
            pointFormat,
            pointGroup,
            dateRange,
            timeRange,
            reqMethod,
            sourceTypeId
        } = pointData;

        this.pointType = pointType || '';
        this.pointDeviceType = pointDeviceType || '';
        this.pointDevice = pointDevice || '';
        this.pointName = pointName || '';
        
        this.valueType = valueType || '';
        this.pointFormat = pointFormat || '';
        this.pointGroup = this.initPointGroup(pointGroup);
        this.dateRange = dateRange || ['',''];
        this.timeRange = timeRange || ['',''];
        this.reqMethod = reqMethod || '';

        if (!sourceTypeId) {
            this.sourceTypeId = this.createdId();
        } else {
            this.sourceTypeId = sourceTypeId;
        }
    }

    public updateProps(pointData: any) {
        if (!_.isObject(pointData)) {
            return;
        }
        for (const propKey of Reflect.ownKeys(pointData)) {
            if (Reflect.has(this, propKey)) {
                if (propKey === 'pointGroup') {
                    this.pointGroup = [];
                    this.initPointGroup(pointData[propKey]);
                }
                this[propKey] = pointData[propKey];
            }
        }
    }

    private initPointGroup(pointGroup: Point[]): Point[] {
        if (!pointGroup) {
            return [];
        }
        const { length } = pointGroup;
        const newPointGroup: any[] = [];
        if (length) {
            for (const point of pointGroup) {
                newPointGroup.push(new Point(point));
            }
        }
        return newPointGroup;
    }

    /**
     * 
     */
    // private updatePointGroup() {

    // }

    public getPartAllProps(): Model.PointModel {
        return {
            sourceType: this.sourceType,

            pointType: this.pointType,
            pointDeviceType: this.pointDeviceType,
            pointDevice: this.pointDevice,
            pointName: this.pointName,

            valueType: this.valueType,
            pointFormat: this.pointFormat,
            pointGroup: this.pointGroup.map( (point) => {
                return point.getPartAllProps();
            }),
            dateRange: this.dateRange,
            timeRange: this.timeRange,
            reqMethod: this.reqMethod,
            sourceTypeId: this.sourceTypeId
        }
    }

    /**
     * Fixme 
     */
    private createdId(): string {
        return 'static_' + new UUID().init();
    }

}